#about{
    margin-top: 400px;
    width: 80%;
    z-index: 2;
    position: relative;
    @include clearfix;
    
    aside{
        width:645px;
        height: 700px;
        background:url("#{$imageBase}vektors.png") no-repeat;
        position: relative;
        left: 180px;
        
         @media (max-width: $capm){
            margin-top: 50px;
             display: none;
        }
    }
    figure{
        position: absolute;
        top: 168px;
        right: 158px;
    }
    article{
        @media (max-width: $capm){
            width: 55%;
            margin-left: 15%;
        }
    }
}

.social{
    margin-top: 100px;
    .fa{
        font-size: 20px;
    }
    a{
        margin-right: 50px;
    }
}
.skills{
    .skill{
        opacity: 0.4;
    }
    .skill:hover{
        opacity: 1;
    }
}

.skill{
    position: absolute;
    cursor:default;
    transition: opacity 500ms ease;
    
     &:nth-child(1) {
        top: -30px;
        left: 42%;
        }
      &:nth-child(2) {
        top: 55px;
        right: 0%;
        }
      &:nth-child(3) {
        top: 165px;
        right: -15%;
        }
      &:nth-child(4) {
        top: 330px;
        right: -18%;
        }
      &:nth-child(5) {
        top: 501px;
        right: -13%;
        }
      &:nth-child(6) {
        top: 607px;
        right: 11%;
        }
      &:nth-child(7) {
        top: 697px;
        left: 35%;
        }
      &:nth-child(8) {
        top: 607px;
        left: 10%;
        }
      &:nth-child(9) {
        top: 501px;
        left: -20%;
        }
      &:nth-child(10) {
        top: 330px;
        left: -17%;
        }
      &:nth-child(11) {
        top: 158px;
        left: -20%;
        }
      &:nth-child(12) {
        top: 55px;
        left: 7%;
        }
    
    strong{
        font-weight: normal;
    }
    
    &:hover p{
        display: block;
    }
    
    p{
        display: none;
        position: absolute;
        background: rgba(255,255,255,0.9);
        border-radius: 10px;
        box-shadow: $shadow;
        padding: 10px;
        z-index: 10;
        color: #000;
    }
}
#process{
    width: 80%;
    margin-top: 300px;
    
    ul{
        margin: 0;
        padding: 0;
        
        &:hover li{
            opacity: 0.5;
        }
        &:hover li:hover{
            opacity: 1;
        }
        @include clearfix;
    }
    li{
        width: 21%;
        float: left;
        padding: 0px;
        margin: 0px;
        list-style: none;
        margin-right: 5%;
        transition: opacity 500ms ease;
        display: block;
        
        &:last-child{
            margin: 0px;
        }
    }
    blockquote{
        display: block;
        text-align: center;
        color: $accent;
        font-size: 1.5em;
        padding: 120px 0px;
    }
    
}




#work{
    position: relative;
    padding-bottom: 100px;
    
    h3{
        float: right;
        margin-right: 20%;
        z-index: 3;
        margin-left: 60%;
        position: relative;
    }
    .container{
        z-index: 3;
        position: relative;
        text-align: center;
        width: 71%;
        
        @media screen and (max-width: $capm){
            width:90%;
        }
    }
    
    .content-bg{
        top: 0px;
        width: 100%;
        height: 100%;
        min-height: 800px;
        transform: skew(0,-3deg);
    }
    .highlight-bg{
        top: 120px;
        width: 100%;
        min-height: 800px;
        height: 100%;
        transform: skew(0,8deg);
        transform-origin: top;
    }
    .container a{
        margin: 0px 25px 26px 0px;
        line-height: 0px;
        float: left;
        
        img{
            width: 100%;
        }
        @media screen and (max-width: $capm){
            width:30%;
        }
    }
    /*
    a.luna{
        float: right;
        
    }*/
    span{
        float: left;
        width: 53.3%;
        
        @media (max-width: $capm){
            width: 50%;
        }
    }
}

#dribbble-clips{
    display: block;
    width: 60px;
    height: 500px;
    color: #fff;
    text-shadow: 1px 1px 0px rgba(0,0,0,0.2);
    background:linear-gradient(to bottom, #ec4989 20%,  #a038f6 100%);
    position: absolute;
    z-index: 1;
    font-size: 2.5em;
    text-align: center;
    transition: top 500ms ease;
        
    left: 10%;
    top: -50px;
    
    &:after{
        position: absolute;
        right: -20px;
        top: 0px;
        display: block;
        content: " ";
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 500px 20px 0 0;
        border-color: rgba(0,0,0,0.2) transparent transparent transparent;
    }
    span{
        position: absolute;
        left: -71px;
        top: 129px;
        font-size: 0.4em;
        display: block;
        width: 200px;
        transform: rotate(-90deg);
    }
    
    &:hover{
        top: -200px;
    }
}

#contact{
    text-align: center;
    margin-top: 200px;
    padding-bottom: 200px;
    font-size: 1.3em;
    position: relative;
    h3{
        display: inline-block;
    }
    a{
        margin-right: 50px;
        &:last-child{
            margin: 0px;
        }
    }
}

// ajax container
#results{
    height: 100%;
}


