.imac{
    width: 627px;
    height: 496px;
    background:url("#{$imageBase}imac.png") no-repeat;
    position: relative;
    
    .inner-clip{
        width:580px;
        height: 330px;
        left: 24px;
        position: relative;
        background: $accent;
        overflow: hidden;
        top: 25px;
    }
    img{
        position: absolute;
        width: 100%;
        height: auto;
        animation: img-scroll 35s infinite alternate cubic-bezier(0.65, 0.05, 0.36, 1);
    }
    &:after{
        content: " ";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        background:url("#{$imageBase}imac.png") no-repeat;
        top: 0px;
    }
    &:hover img{
        animation-play-state: paused !important;
    }
}
@keyframes img-scroll {
    0% {
        transform: translateY(0px) translateZ(0); 
    }
    100% {
        transform: translateY(-70%) translateZ(0); 
    } 
}

