.window{
    border: 10px solid #06B805;
    padding: 5%;
    position: relative;
    z-index: 10;
    box-sizing: border-box;
    &:after, &:before{
        position: absolute;
        content: " ";
        display: block;
        top: 10px;
        left: 10px;
        width: 100%;
        height: 100%;
        z-index: 2;
        border: 10px solid rgba(0,0,0,0.15);
    }
    &:before{
        border-color: #06B805;
        z-index: 3;
        top:-10px;
        left: -10px;
    }
    h4{
        font-size: 6em;
        position: relative;
        z-index: 4;
        margin: 50px 100px;
        color: #fff;
    }
}
